body {
    align-items: center;
    background-color: #fff;
    display: flex;
    height: 100vh;
    justify-content: center;
}

.botIcon {
    position: fixed;
    left: -200px;
    top: 180px;
    width: 400px;
}

.inputField {
    position: sticky;
    z-index: 9999;
    left: -150px;
    top: 730px;
    font-family: Ubuntu, sans-serif;
    font-size: x-large;
    align-items: center;
    background-color: rgba(255, 255, 255, 0.9);
    box-shadow: 12px 12px 16px 0 rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: center;
    margin-right: 4rem;
    padding-left: 15px;
    width: 880px;
    height: 50px;
}

.otherElement {
    position: relative;
    font-family: Ubuntu, sans-serif;
    font-size: xx-large;
    text-align: left;
    box-shadow: 2px 2px 3px 0 rgba(0, 0, 0, 0.25);
    border-radius: 10px;
    display: flex;
    justify-content: left;
    margin-right: 4rem;
    width: 25em;
    overflow-y: auto;
    border: 0.1pt solid lightgray;
    padding: 45px;
    top: -6em;
}

.embed-submit-field {
    position: relative;
}

input {
    width: 100%;
    padding: 5px;
}

button {
    z-index: 9999;
    position: absolute;
    right: 80px;
    top: 7px;

    width: 70px;
    height: 45px;

    -moz-appearance: none;

    border: none;
    background: #ededed;
    border-radius: 3px;
    padding: 10px;
  margin-top: 2px;

    transition: all .2s;
}

button:hover {
  background-color: #67AB67;
  color: white;
}

button:active {
  background-color: #468546;
  color: white;
}

